.page-banner {
  padding: 80px 0 40px 0;
  position: relative;
}
.page-banner__content {
  position: relative;
  z-index: 2;
}
.page-banner__bg-image {
  background-size: cover;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.headline {
  text-transform: uppercase;
}
.headline--small {
  font-size: 1.8rem;
}
.headline--medium {
  font-size: 2rem;
}
.headline--large {
  font-size: 2.8rem;
}

.btn {
  padding: 0.2em 1em;
  appearance: none;
  background-color: #000;
  border-radius: 5px;
  color: #fff;
  line-height: 100%;
  text-decoration: unset;
}
.btn--small {
  font-size: 1.4rem;
}
.btn--medium {
  font-size: 1.6rem;
}
.btn--large {
  font-size: 2rem;
}
.btn--blue {
  background-color: #0d3b66;
}
.btn--orange {
  background-color: #ee964b;
}
.btn--dark-orange {
  background-color: #f95738;
}

.layout-two-col {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.layout-two-col__column {
  flex: 1 1 200px;
}